                                {/*<ul className="row text-white carousel-indicators" style={{listStyle: "none"}}>*/}
                                {/*         <li className="col-3 py-2 "  data-target="#carouselIndicator" data-slide-to="0"><button>Monthly Customer Registration</button></li>*/}
                                {/*         <li className="col-3 py-2 rounded"  data-target="#carouselIndicator" data-slide-to="1"><button className="rounded">Registered Customers VS Active Customers</button></li>*/}
                                {/*         <li className="col-3 py-2"  data-target="#carouselIndicator" data-slide-to="2"><button>Revenue Per Customer</button></li>*/}
                                {/*         <li className="col-3 py-2"  data-target="#carouselIndicator" data-slide-to="3"><button>Revenue Per Float Advance</button></li>*/}
                                {/*        <li  className="col-3 py-2" data-target="#carouselIndicator" data-slide-to="4"><button>Disbursal Value</button></li>*/}
                                {/*        <li  className="col-3 py-2" data-target="#carouselIndicator" data-slide-to="5"><button>Advances Disbursed</button></li>*/}
                                {/*        <li  className="col-3 py-2" data-target="#carouselIndicator" data-slide-to="6"><button>Advances Completed</button></li>*/}
                                {/*        <li  className="col-3 py-2" data-target="#carouselIndicator" data-slide-to="7"><button>OnTime Payment</button></li>*/}
                                {/*        <li  className="col-3 py-2" data-target="#carouselIndicator" data-slide-to="8"><button>{'OnTime Payment - ' + this.state.country_code + ' Market'}</button></li>*/}
                                {/*        <li  className="col-3 py-2" data-target="#carouselIndicator" data-slide-to="9"><button>{'Revenue - ' + this.state.country_code + ' Market'}</button></li>*/}
